.main{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.ava{
    margin-left: 20px;
    margin-bottom: 20px;
}
.photo{
    /* margin-top: 20px; */
    width: 100px;
    height: 100px;
    border-radius: 50%;
}
.info{
    
    display: flex;
    justify-content: space-between;
    margin-left: 20px;
    margin-bottom: 30px;
    border: 1px solid;
    border-radius: 20px;
    color: rgb(69, 123, 123);
    width: 600px;
}
.info_child{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 14px;
}

